import { Component, h, Host, Prop } from '@stencil/core';

export type SrcDestAlign = 'left' | 'center';

/**
 * @category Utilities
 * @description A src-dest component for displaying source and destination information.
 */

@Component({
    tag: 'atui-src-dest',
    shadow: false,
})
export class AtuiSrcDestComponent {
    /**
     * Title displayed on the left
     */
    @Prop()
    src_title: string;

    /**
     * Text displayed on the left
     */
    @Prop()
    src_text: string;

    /**
     * Subtitle displayed on the left
     */
    @Prop()
    src_subtitle: string;

    /**
     * Title displayed on the right
     */
    @Prop()
    dest_title: string;

    /**
     * Text displayed on the right
     */
    @Prop()
    dest_text: string;

    /**
     * Subtitle displayed on the right
     */
    @Prop()
    dest_subtitle: string;

    /**
     * Aligns the content to the left or center of the container
     */
    @Prop()
    align: SrcDestAlign = 'left';

    render() {
        return (
            <Host
                class={`flex flex-wrap items-center gap-16 ${this.align === 'center' ? 'justify-center' : 'justify-start'}`}
            >
                {this.src_title && (
                    <h4
                        class="h4"
                        data-name="src-title"
                    >
                        {this.src_title}
                    </h4>
                )}
                {(this.src_text || this.src_subtitle) && (
                    <div>
                        {this.src_text && (
                            <p
                                class="font-medium text-dark"
                                data-name="src-text"
                            >
                                {this.src_text}
                            </p>
                        )}
                        {this.src_subtitle && (
                            <p
                                class="text-med"
                                data-name="src-subtitle"
                            >
                                {this.src_subtitle}
                            </p>
                        )}
                    </div>
                )}

                <div class="icon-lg flex-row items-center gap-4 text-light">
                    <span class="material-icons">arrow_back</span>
                    <span class="material-icons">arrow_forward</span>
                </div>

                {(this.dest_text || this.dest_subtitle) && (
                    <div class="text-right">
                        {this.dest_text && (
                            <p
                                class="font-medium text-dark"
                                data-name="dest-text"
                            >
                                {this.dest_text}
                            </p>
                        )}
                        {this.dest_subtitle && (
                            <p
                                class="text-med"
                                data-name="dest-subtitle"
                            >
                                {this.dest_subtitle}
                            </p>
                        )}
                    </div>
                )}
                {this.dest_title && (
                    <h4
                        class="h4"
                        data-name="dest-title"
                    >
                        {this.dest_title}
                    </h4>
                )}
            </Host>
        );
    }
}
